<template>
    <div>
        <div v-for= "(v,i) in arr" :key="i" class="box">
            <p><img :src="v.image_hlarge" alt="">
                
            </p>
            <p>{{v.title}}</p>
           
           
            
             <p v-html="v.content" class="content"></p>
             <p class="content1">by {{v.owner.name}}</p>
        </div>
    </div>
</template>
<script>
export default {
   props:["arr"]
}
</script>
<style scoped>
.box{
    margin: 10px 0;
   
}
.content{
    color: #cccccc;
    font-size: 12px;
    height: 113px;
      white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
    
}
.content1{
 color: #cccccc;
    font-size: 12px;
}
.box img{
    width:200px;
    height:130px;
}


</style>



