<template>
  <div>
      <Header></Header>
    <div class="header">
      <span></span>
      <span></span>
      <span></span>
      <span>APP</span>
  </div>
   <Shouyezujian :arr="data"></Shouyezujian>
  </div>
</template>
<script>
import Header from "../components/header"
import Shouyezujian from "../components/shouyezujian"
export default {
  data(){
      return {
          data:[],
      }
  },
    components:{
            Header,
            Shouyezujian
    },
     created(){
      this.axios.get("/jsondata/abc").then((data)=>{
          
                 // this.arr=data.data.faxianhaodianying ;
                  this.data=data.data.shouye
                 
        })
  
    }

};
</script>
<style scoped>
    .header span{
       background-color: #f6f6f6;
    color: #494949;
  
    text-align: center;
    line-height: 20px;
    padding: 12px 0;
    border-radius: 2px;
  
    
    width: 136px;
    margin: 3px;
    }
    .header{
        margin: 20px 0 0 0;
    padding-top: 6px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    }
</style>



