<template>
    <div >
        <div class="box">
            <p>
            <img src="../../static/img/douban.png" alt="" style="width:48px;height:20px" @click="skip()">
            <img src="../../static/img/list.png" alt="" style="width:24px;height:18px"  @click="fun()">
        </p>
        
        <p class="open">APP</p>
        </div>
        
    </div>
</template>
<script>

export default {
    methods: {
        fun(){
            this.$router.push("/nav")
           
        },
        skip(){
            this.$router.push("/index")
        }
    },
}
</script>
<style scoped>
.box{
    display: flex;
    justify-content: space-between;
  
   
  border-bottom: 1px solid #ccc;
}
.open{
    background-color:rgb(69, 112, 5);
    line-height: 30px;
    color: white;
}





</style>



