<template>
  <div>
    <Loading v-if="bool"></Loading>
    <div v-else>
      <Header></Header>

      <Bookscom text="" :data="xugou"></Bookscom>
      <Bookscom text="" :data="feixugou"></Bookscom>
      <Bookscom text="" :data="newMovie"></Bookscom>
      <p></p>
      <ul class="box">
        <li v-for="(v,i) in arr" :key="i" :style="{color:v.color}">
          <span>{{v.title}}</span>
        </li>
      </ul>
      <p></p>
      <ul class="list">
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <Bottom></Bottom>
    </div>
  </div>
</template>
<script>
import Bookscom from "../components/bookscom";
import Header from "../components/header";
import Loading from "../components/loading";
import Bottom from "../components/bottom";
export default {
  components: {
    Header,
    Bookscom,
    Loading,
    Bottom
  },
  data() {
    return {
      arr: [],
      data: []
    };
  },
  methods: {},
  computed: {
    xugou() {
      let nerArr = this.data.slice(29, 37);
      nerArr.map(item => {
        item.rating.average = item.rating.average / 2;
      });
      return nerArr;
    },
    feixugou() {
      let nerArr = this.data.slice(8,17);
      nerArr.map(item => {
        item.rating.average = item.rating.average / 2;
      });
      return nerArr;
    },
    newMovie() {
      let nerArr = this.data.slice(17,26);
      nerArr.map(item => {
        item.rating.average = item.rating.average / 2;
      });
      return nerArr;
    }
  },
  created() {
    this.axios.get("/jsondata/abc").then(data => {
      if (this.arr == "" && this.data == "") {
        this.bool = true;
      }
      this.bool = false;
      this.arr = data.data.faxianhaodianying;
      this.data = data.data.movie;
    });
  }
};
</script>
<style scoped>
.box {
  white-space: nowrap;
  overflow-x: auto;
  padding: 15px 15px 43px 15px;
  font-size: 0;
  margin-left: -5px;
}
.box li {
  margin: 0 0 8px 8px;
  font-size: 0.94rem;
  display: inline-block;
  border-radius: 0.25rem;
  border: solid 1px;
  vertical-align: middle;
}
.box span {
  height: 50px;
  line-height: 50px;
  padding: 0 1.55rem;
  letter-spacing: 0.1em;
  overflow: auto;
  display: block;
  text-align: center;
}
.list {
  padding: 1.12rem 0 1.88rem;
  color: #eee;
  font-size: 0.9rem;
  overflow: hidden;
  margin-left: 15px;
}
.list li {
  width: 39.5%;
  border-top: solid 1px #eee;
  border-right: solid 1px #eee;
  float: left;
  padding-right: 1.12rem;
  height: 2.64rem;
  list-style: none;
  color: #42bd56;
}
</style>


