<template>
    <div>
        <header>
          <div @click="btnclose()"><span></span></div>
          <div><input type="text" :class="isChange?'search-h':'search-x'" @focus="btnChange()" @blur="btnChange()"></div>
        </header>
        <div class="contain"> 
           
            <div @click= "movie(i)" v-for= "(v,i) in arr" :key= "i" class="navCen">
                <el-col :span="6">
                    <div class="grid-content bg-purple">
                    <p :style= "{color:v.color}" class="titleSize">{{v.title}}</p>
                            <p class="contentSize">{{v.content}}</p>
                </div>
                </el-col>
            </div>
          
  
  
 

            
    </div>
    <div class="login">
        <p></p>
    
    </div>
     <div class="footer">
        <p></p>
        <p>APP</p>
    </div>
       
    </div>
</template>
<script>
import Movie from './movie'
export default {
    data(){
        return {
            arr:[
                {title:"",content:"",color:"blue",tem:"/movie"},
                 {title:"",content:"",color:"red",tem:"/tv"},
                  {title:"",content:"",color:"pink",tem:"/books" },
                   {title:"",content:"",color:"yellow",tem:"/movie"},
                    {title:"",content:"",color:"gray",tem:"/movie"},
                     {title:"",content:"",color:"red",tem:"/movie"},
                      {title:"",content:"",color:"pink",tem:"/movie"},
                       {title:"",content:"",color:"red",tem:"/movie"},
                        {title:"",content:"",color:"yellow",tem:"/movie"},
                         {title:"",content:"",color:"gray",tem:"/movie"},
                          {title:"FM",content:"",color:"blue",tem:"/movie"},
                           {title:"",content:"",color:"#ccc",tem:"/movie"},

            ],
            isChange:true
        }
    },
    components:{
        Movie
    },
     created(){
        
              this.$store.dispatch("axiosfunA")
      
        
    },
    methods: {
        movie(i){
            this.$router.push(this.arr[i].tem)
        },
        btnclose(){
            this.$router.push("/index")
        },
        btnChange(){
            this.isChange=!this.isChange
        }
    },
}
</script>
<style scoped>
.el-row {
    margin-bottom: 20px;
 
  }
  .el-col {
    border-radius: 4px;
  }
  
  .juli{
      margin: 0 40px;
  }
  
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    
  }
  .wen{
      text-align: center;
      line-height:36px; 
      color: #42bd56;
      font-size: 16px;
  }
  .search-h{
        box-sizing: border-box;
    -webkit-appearance: none;
    border: 0;
     background:url("../../static/img/search.png") no-repeat 50%;
    background-color:#f3f3f3;
    background-size: 17.33333px;
    border-radius: 3px;
    width: 242px;
    height: 28px;
    font-size: 14px;
  }
  .search-x{
        box-sizing: border-box;
    -webkit-appearance: none;
    border: 0;
   
    background-color:#f3f3f3;
    background-size: 17.33333px;
    border-radius: 3px;
    width: 242px;
    height: 28px;
    font-size: 14px;
  }
  header{
      border-bottom:1px #ccc solid;
      display: flex;
      justify-content: space-around;
  }
  header span{
      color: #42bd56;
      line-height: 30px;
  }
  .navCen{
      text-align: center;
  }
  .titleSize{
      font-size: 28px;
      font-weight: 700;
  }
  .contentSize{
      color: #cccccc;
      font-size: 14px;
  }
  .login{
      text-align: center;
      line-height: 50px;
      color: #42bd56;
  }
  .footer{
      display: flex;
     justify-content: space-around;
      color: #42bd56;
  }

</style>



