import Vue from 'vue'
import Router from 'vue-router'
import Nav from '../pages/nav'
import Detail from '../pages/detail'
import Tv from '../pages/tv'
import Movie from '../pages/movie'
import Books from '../pages/books'
import Index from "../pages/index"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/nav',
      name: 'Nav',
      component: Nav
    },
    {
      path: '/Detail',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/tv',
      name: 'Tv',
      component: Tv
    },
    {
      path: '/movie',
      name: 'Movie',
      component: Movie
    },
    {
      path: '/books',
      name: 'Books',
      component: Books
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {path:"/*",redirect:"/nav"}
  ]
})



