<template>
  <div>
    <div v-if="arr.subtype=='books'">
      <Header></Header>
      <div class="header">
        <div class="left">
          <p>{{arr.original_title}}</p>
          <p>{{arr.title}}</p>
          <p>
            <el-rate
              v-model="star"
              disabled
              show-score
              text-color="#ff9900"
              score-template="{value}"
            ></el-rate>
            <span>{{arr.rating.numRaters}}</span>
          </p>

          <p class="info">
            {{arr.author[0]}}/{{arr.author[1]}}/{{arr.publisher}}
            {{arr.pages}}/{{arr.binding}}/{{arr.price}}/{{arr.pubdate}}
          </p>
        </div>
        <P class="imgSize">
          <img :src="arr.images.small" alt />
        </P>
      </div>
      <p class="justfi">
        <span></span>
        <span>{{arr.price}}</span>
      </p>
      <p class="justfi">
        <span></span>
        <span>{{arr.price}}</span>
      </p>
      <p class="read">
        <span></span>
        <span></span>
        <span></span>
      </p>

      <section>
        <p>{{arr.title}}</p>
        <div v-if="isShow" class="yincang">
          {{sub}}...
          <span class="open" @click="open()">()</span>
        </div>
        <div v-else>
          {{arr.summary}}
          <span @click="open()" class="open">()</span>
        </div>
      </section>
    </div>
    <!--  -->
    <div v-else-if="arr.subtype=='movie'">
      <Header></Header>
      <div class="header">
        <div class="left">
          <p>{{arr.title}}:{{arr.original_title}}</p>
          <p>
            <el-rate
              v-model="star"
              disabled
              show-score
              text-color="#ff9900"
              score-template="{value}"
            ></el-rate>
            <span style="color:#ccc;font-size:14px">{{arr.collect_count}}</span>
          </p>

          <p
            class="info"
          >{{arr.genres[0]}}/{{arr.genres[1]}}/{{arr.directors[0].name}}()/{{arr.casts[0].name}}/{{arr.casts[1].name}}/{{arr.casts[2].name}}/{{arr.year}}</p>
        </div>
        <P class="imgSize">
          <img :src="arr.images.small" alt />
        </P>
      </div>
      <p class="read">
        <span></span>
        <span></span>
      </p>
      <section>
        <p>{{arr.title}}</p>
        <p>79</p>

        <!-- {{arr.produce}} -->
        <!-- <span @click="open()" class="open">()</span> -->
      </section>
      <p></p>
      <ul class="derictors">
        <li>
          <img :src="arr.directors[0].avatars.small" alt />
          <p>{{arr.directors[0].name}}</p>
          <p></p>
        </li>
        <li v-for="(v,i) in arr.casts" :key="i">
          <img :src="v.avatars.small" alt />
          <p>{{v.name}}</p>
          <p></p>
        </li>
      </ul>
    </div>
    <div>
      <!--  -->
      <p></p>
      <div v-if="com">
        <p>
          <span></span>
          <input type="text" v-model="name" />
        </p>
        <p>
          <span></span>
          <input style="resize" v-model="inputAdd" />
        </p>
          <p>
        
      </p>
      </div>
      <div v-else>
        <p>
          <span></span>
          <input type="text" v-model="resName"/>
        </p>
        <p>
          <span></span>
          <input style="resize" v-model="resInput"/>
        </p>
         <p>
        
      </p>
      </div>
    <p>
      <span @click="add()" style="background:green;color:white"></span>
    <span style="background:red;color:white" @click="response()"></span>
    
    
    </p>
      <div v-for="(v,i) in comments" :key="i" v-show="v.del" class="list">
        <div class="comm">
          <p style="display:flex">
            
            <span class="name">{{v.mingzi}}</span>
            <el-rate 
              v-model="value"
              disabled
              show-score
              text-color="#ff9900"
              score-template="{value}"
            ></el-rate>
            
          </p>
             <p class="content">{{v.content}}</p>
          <div>
            <p class="name"><span>{{v.resName1}}</span></p>
            <p class="content">{{v.resContent}}</p>
          </div>
          <p>
            
            <span @click="commit(i)" style="background:green;color:white"></span>
            <span @click="del(i)" style="background:yellow;color:white"></span>
          </p>
        </div>
      </div>
    </div>
    <Bottom></Bottom>
  </div>
</template>
<script>
import Bottom from "../components/bottom";
import Header from "../components/header";
export default {
  components: {
    Header,
    Bottom
  },
  data() {
    return {
      arr: this.$route.query.id,
      isShow: true,
      comments: [],
      inputAdd: "",
      name: "",
      text1: [],
      value: 3.7,
      com: true,
      name1:'',
      res:'',
      resInput:"",
      resName:""
    };
  },
  computed: {
    sub() {
      return this.arr.summary.substr(0, 30);
    },
    //  sub1() {
    //   return this.arr.produce.substr(0, 30);
    // },

    star() {
      return this.arr.rating.average;
    }
  },
  methods: {
    open() {
      this.isShow = !this.isShow;
    },
    add() {
      this.com=true;
      this.comments.map(item => {
        if (item.content == this.inputAdd) {
          this.inputAdd = "";
          ///console.log("111")
          return;
        }
        return;
      });
      if (this.inputAdd == "") {
        return;
      }
      this.comments.push({
        content: this.inputAdd,
        mingzi: this.name,
        resContent:"",
        del: true
      });
    },
    del(i) {
      this.comments[i].del = false;
      this.comments[i] = "";
    },
    response() {
      this.com = false;
      
     
      
    },
    commit(i){
     
         this.comments[i].resContent=this.resInput;
         this.comments[i].resName1=this.resName;
    }
  }
};
</script>
<style scoped>
.imgSize img {
  width: 100px;
  height: 140px;
  margin-top: 45px;
}
.header {
  display: flex;
  justify-content: space-between;
}
.header {
  display: flex;
}
.info {
  padding-right: 24px;
  font-size: 14px;
  line-height: 1.6;
}
.justfi {
  display: flex;
  justify-content: space-between;
  padding: 0 18px;
}
.justfi :nth-child(1) {
  color: #42bd56;
}
.justfi :nth-child(2) {
  color: #ccc;
}
.read {
  display: flex;
  justify-content: space-between;
  padding: 0 18px;
}
.read span {
  height: 30px;
  line-height: 30px;
  display: block;
  border: 1px solid #ffb712;
  border-radius: 3px;
  margin-right: 10px;
  color: #ffb712;
  font-size: 15px;
  text-align: center;
  -webkit-box-flex: 1;
  width: 88px;
}
section p {
  color: rgb(119, 112, 112);
  margin: 0 0 15px;
  font-size: 15px;
}

.open {
  color: #42bd56;
}
ul li {
  list-style: none;
}
.derictors {
  display: flex;
  display: -webkit-flex;
  overflow-x: auto;
}
.derictors img {
  width: 100px;
  height: 140px;
  margin-right: 10px;
}
.derictors p {
  font-size: 12px;
  color: #ccc;
  text-align: center;
}

.content {
  font-size: 14px;
  color: #cccccc;
}
.name{
  font-size: 18px;
  color: pink;
  font-weight: 100;
}
.list {
  border: 1px solid #cccccc;
  margin-bottom: 10px;
}
</style>


