import Vue from "vue"
import Vuex from "vuex"
import axios from 'axios'

Vue.use(Vuex)
export  const store = new Vuex.Store({
    state:{
        arr:[]
    },
    mutations:{
        mufunC(state,payload){
           
            state.arr=payload
 
     
 }
    },
    actions:{
        axiosfunA(context){
         
           axios.get("/jsondata/abc").then((data)=>{
               
        context.commit("mufunC",data.data)
    })
    }
    }
})


