<template>
    <div>
        <div class="title">
            <p>{{text}}</p>
            <p></p>
        </div>
        <div class="list">
            <div v-for= "(v,i) in data" :key="i" @click="Detail(v)">
                <img :src= "v.images.small">
                <P class="over">{{v.title}}</P>
                <p><el-rate
                    v-model="v.rating.average"
                    disabled
                    show-score
                    text-color="#ff9900"
                    score-template="{value}"
                    >
                    </el-rate>
                </p>
            </div>
      </div>
    </div>
</template>
<script>
export default {
    data() {
      return {
        //arr:this.$store.state.arr.books,
        value:3,
        
      }
    },
    
      computed:{
       
        
     
      },
      props:["data","text"],
   
    methods: {
        Detail(con){
            this.$router.push({path:"/detail",query:{id:con}})
           
        }
        
    }, 
   
    
    
}
</script>
<style scoped>
    .title{
        display: flex;
        justify-content: space-between;
        padding: 0 18px;
    }
    .title :nth-child(2){
        color: aqua;
    }
    .list{
        display:flex;
        display: -webkit-flex;
    overflow-x: auto;
   
    }
    .list img{
        width:100px;
        height:140px;
    }
    .list div{
        margin:0 10px;
        width:100px;
    }
   
.over{
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
}
</style>



