<template>
  <div>
    <Loading v-if="bool"></Loading>
    <div v-else>
      <Header></Header>
      <Bookscom text="|" :data="xugou"></Bookscom>
      <Bookscom text="|" :data="feixugou"></Bookscom>

      <!--  -->
      <div>
        <p class="store">
          <span></span>
          <span></span>
        </p>

        <!-- <p>{{zhuanyi[0].image}}</p> -->
      </div>
      <div class="bass">
        <!-- <img :src="xugou[2].images.small" alt=""> -->
        <div class="right">
          <p>
            ...
            <span>132</span>
          </p>
          <p></p>
        </div>
      </div>
      <div>
        <ul class="findGk">
          <li v-for="(v,i) in zhuanyi" :key="i" @click="detail(v)">
            <img :src="v.images.small" />
            <p class="over">{{v.title}}</p>
            <p>{{v.price}}</p>
          </li>
        </ul>
      </div>
      <p></p>

      <ul class="box">
        <li v-for="(v,i) in goodbooks" :key="i" :style="{color:v.color}">
          <span>{{v.title}}</span>
        </li>
      </ul>
      <p></p>
      <ul class="list">
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <Bottom></Bottom>
    </div>
  </div>
</template>
<script>
import Loading from "../components/loading";
import Bookscom from "../components/bookscom";
import Header from "../components/header";
import Bottom from "../components/bottom"
// import Detail from "./detail"
export default {
  components: {
    Header,
    Bookscom,
    Loading,
    Bottom
  },
  data() {
    return {
      arr: [],

      goodbooks: [],
      bool:true

    };
  },
  computed: {
    xugou() {
      let nerArr = this.arr.slice(0, 9);
      nerArr.map(item => {
        item.rating.average = item.rating.average / 2;
        item.subtype = "books";
      });
      return nerArr;
    },
    feixugou() {
      let nerArr = this.arr.slice(9, 18);
      nerArr.map(item => {
        item.rating.average = item.rating.average / 2;
        item.subtype = "books";
      });
      return nerArr;
    },
    store() {
      return this.arr.slice(19, 28);
    },

    zhuanyi() {
      let change = this.arr;
      change.map(item => {
        // item.images.small.replace(/[\\]/g,'');

        item.subtype = "books";
      });
      return change;
    }
  },
  created() {
    this.axios.get("/jsondata/abc").then(data => {
      if (this.arr == "" && this.goodbooks == "") {
        // this.bool=true
      }
      this.bool = false;
      this.arr = data.data.books;
      this.data = data.data.goodBooks;
    });
  },
  methods: {
    detail(con) {
      this.$router.push({ path: "/detail", query: { id: con } });
    }
  }
};
</script>
<style scoped>
.box {
  white-space: nowrap;
  overflow-x: auto;
  padding: 15px 15px 43px 15px;
  font-size: 0;
  margin-left: -5px;
}
.box li {
  margin: 0 0 8px 8px;
  font-size: 0.94rem;
  display: inline-block;
  border-radius: 0.25rem;
  border: solid 1px;
  vertical-align: middle;
}
.box span {
  height: 50px;
  line-height: 50px;
  padding: 0 1.55rem;
  letter-spacing: 0.1em;
  overflow: auto;
  display: block;
  text-align: center;
}
.list {
  padding: 1.12rem 0 1.88rem;
  color: #eee;
  font-size: 0.9rem;
  overflow: hidden;
  margin-left: 15px;
}
.list li {
  width: 39.5%;
  border-top: solid 1px #eee;
  border-right: solid 1px #eee;
  float: left;
  padding-right: 1.12rem;
  height: 2.64rem;
  list-style: none;
  color: #42bd56;
}
.store {
  display: flex;
  justify-content: space-between;
  margin: 0 18px;
}
.store span:nth-child(2) {
  color: #42bd56;
}
.bass img {
  width: 100px;
  height: 132px;
  margin-right: 15px;
}
.bass {
  display: flex;
}
.right p:nth-child(1) {
  font-size: 18px;
}
.right p:nth-child(2) {
  font-size: 12px;
  color: #cccccc;
}
.right p span {
  color: rgb(228, 15, 15);
  font-size: 14px;
}
.findGk {
  list-style: none;
  display: flex;
  display: -webkit-flex;
  overflow-x: auto;
}
.findGk li img {
  width: 100px;
  height: 140px;
}
.findGk li {
  margin: 0 10px;
  width: 100px;
}
.over {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}
.findGk li p:nth-child(3) {
  font-size: 10px;
  color: #cccccc;
  text-align: center;
}
</style>


